.bold {
    font-weight: bold;
    font-size: large;
}

.italicize {
    font-style: italic;
    font-size: large;
}

.indent {
    padding-left: 32px;
}

.fadein-left {
    /* @keyframes duration | easing-function | delay |
    iteration-count | direction | fill-mode | play-state | name */
    animation-name: fadein-left;
    animation-duration: 1s;
    animation-timing-function: ease-out;
    animation-fill-mode: both;
}

.fadein-right {
    /* @keyframes duration | easing-function | delay |
    iteration-count | direction | fill-mode | play-state | name */
    animation-name: fadein-right;
    animation-duration: 1s;
    animation-timing-function: ease-out;
    animation-fill-mode: both;
}

.home-section {
    display: flex;
    padding: 10px;
    margin-bottom: 5px;
}

.home-section>div:last-child {
    margin-left: 15px;
}

.home-section-img-container img {
    display: flex;
    width: 25vw;
    height: 25vw;
    max-height: 320px;
    max-width: 320px;
    aspect-ratio: 1/1;
    object-fit: contain;
}

.home-section-img-container {
    display: flex;
    flex-wrap: wrap;
    margin-top: 32px;
}

.home-section-content {
    border-radius: 32px;
    padding: 16px;
}

.home-section-skills {
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 0;
}

.home-section-skills li {
    min-width: 100px;
    text-align: center;
    padding: 10px;
    margin: 5px;
    border: solid 1px white;
    border-radius: 15px;
}

.story-img-container {
    text-align: center;
}

.story-img {
    max-height: 320px;
}

.show-hide-btn {
    cursor: pointer;
    text-decoration: underline;
    user-select: none;
    color: var(--linkColor);
}

.show-hide-btn:hover {
    color: var(--warnBGColor);
}

#sticky-container {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
}

#canvas-container {
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: -1;
    margin: 0;
    padding: 0;
}

@keyframes fadein-left {
    from {
        opacity: 0;
        transform: translate(-10vw, 0);
    }

    to {
        opacity: 1;
        transform: translate(0, 0);
    }
}

@keyframes fadein-right {
    from {
        opacity: 0;
        transform: translate(10vw, 0);
    }

    to {
        opacity: 1;
        transform: translate(0, 0);
    }
}

@media (orientation: portrait) {
    .home-section {
        flex-direction: column;
        align-items: center;
    }

    .home-section-img-container img {
        width: 50vw;
        height: 50vw;
    }

    .story-img {
        max-height: none;
        width: 80%;
    }

    .indent {
        padding-left: 8px;
    }
}

.carousel-inner {
    position: relative;
    width: 80%;
    margin: auto;
    overflow: hidden;
    padding-bottom: 32px;
}

.carousel-item-next,
.carousel-item-prev,
.carousel-item.active {
    visibility: visible;
}

.carousel-item {
    position: relative;
    visibility: hidden;
    display: block;
    float: left;
    width: 100%;
    margin-right: -100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transition: transform .6s ease-in-out;
    padding: 1em;
}

.carousel-control-prev-icon {
    width: 4em;
    height: 4em;
}

.carousel-control-next-icon {
    width: 4em;
    height: 4em;
}