.project-card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.project-card {
    display: flex;
    flex-direction: column;
    text-align: center;
    max-width: 364px;
    border: 1px solid gray;
    padding: 15px;
    border-radius: 15px;
    box-shadow: 3px 3px 15px 0px black;
    margin: 15px;
    height: fit-content;
}

.project-card-img {
    width: 100%;
    height: 160px;
    border-radius: 15px;
}

.project-card-highlights {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.project-card-highlight{
    padding: 6px;
    margin: 3px;
    text-align: center;
    background-color: var(--linkColor);
    color: black;
    border-radius: 32px;
    min-width: 160px;
}

.project-card-desc {
    text-align: left;
    height: 200px;
    overflow: auto;
    padding-right: 3px;
}

.content-wrapper {
    padding-bottom: 60px;
}