:root {
  --mainBGColor: #282c34;
  --errorBGColor: #ff3f31;
  --warnBGColor: #ffc107;
  --successBGColor: #3baf3f;
  --activeBGColor: #333944;
  --pressedBGColor: #1E2129;
  --inactiveBGColor: #80858E;

  --inactiveFontColor: #333333;

  --linkColor: #61dafb;
}

body {
  background-color: var(--mainBGColor);
  color: #fff;
}

a {
  color: var(--linkColor);
}

.center {
  text-align: center;
}

.content-wrapper {
  max-width: 80vw;
  margin: auto;
  overflow-x: hidden;
}

@media (orientation: portrait) {
  .content-wrapper {
    max-width: 95vw;
    margin: auto;
    overflow-x: hidden;
  }
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(242, 242, 242, .1);
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--linkColor);
}