.navbar {
    text-align: center;
    box-shadow: 0px 5px 5px var(--pressedBGColor);
    margin-bottom: 10px;
    border-bottom: 1px solid var(--pressedBGColor);
    font-size: calc(10px + 1vmin);
    background-color: var(--mainBGColor);
}

.navbar-header {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
}

.navbar-header img {
    width: calc(32px + 4vmin);
    height: calc(32px + 4vmin);
    aspect-ratio: 1/1;
    margin-top: auto;
    margin-Bottom: auto;
    margin-left: 1vmin;
    border-radius: 16px;
}

.navbar-header h1 {
    flex: 1;
}

.navbar-subheader {
    font-size: 14px;
    width: 100%;
}

.navbar-links {
    background-color: var(--mainBGColor);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    flex-wrap: wrap;
    width: 100%;
}

.navbar-links .active-link {
    border-color: var(--linkColor);
    text-decoration: underline;
}

.navbar-links a {
    flex: 1;
    padding: 10px;
    border: 1px solid var(--pressedBGColor);
    border-radius: 15px;
    margin: 5px;
    user-select: none;
    text-decoration: none;
}

.navbar-links a:hover {
    background-color: var(--activeBGColor);
    color: var(--warnBGColor);
    border-color: var(--warnBGColor);
}

.navbar-links a:active {
    background-color: var(--pressedBGColor);
    border-color: var(--activeBGColor);
}

.navbar-socials {
    display: flex;
    justify-content: space-around;
    min-width: 100px;
    align-items: center;
    flex-wrap: wrap;
}

.fa {
    font-size: 24px;
    width: 36px;
    height: 36px;
    text-align: center;
    text-decoration: none;
    margin: 5px 2px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 5px;
    border: 0.3px solid black;
}

.fa:hover {
    opacity: 0.7;
}

.fa-facebook {
    background: #3B5998;
    color: white;
}

.fa-linkedin {
    background: #007bb5;
    color: white;
}

.fa-github {
    background: #111;
    color: white;
}

@media (orientation: portrait) {
    .navbar-subheader {
        margin-top: 0;
        margin-bottom: 10px;
    }
}